.product-list-section ul li {
    display: flex;
    flex-direction: column;
    width: 100%;
    border: 1px solid #ddd;
    border-radius: 10px;
    gap: 10px;
}

.product-list-section ul {
    display: grid;
    grid-template-columns: auto auto auto auto;
    gap: 30px;
    padding: 0;
}

.Home-Page-Product-title {
    color: #292b2c;
    font-family: 'Roboto';
    font-weight: 700;
    font-size: 35px;
    position: relative;
}

.Home-Page-Product-title::before {
    content: "";
    position: absolute;
    left: 49%;
    width: 50px;
    height: 1px;
    background-color: #FF324D;
    bottom: -15px;
    right: 0;
}

.product-list-section {
    padding: 35px;
}

h2.price {
    color: #FF324D;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    padding-bottom: 10px;
}

.product-list-section a {
    color: #292b2c;
    text-decoration: none;
    font-size: 16px;
    font-weight: 600;
}

.product-list-section ul li img {
    width: 100%;
}

.Single-product-page {
    padding: 69px 0px;
}
.Single-product-page img {
    width: 100%;
}
.Single-product-page .p-name {
    font-size: 28px;
    color: #292b2c;
    font-weight: 600;
    line-height: 1.2;
}

.Single-product-page h2.price {
    font-size: 26px;
}
.p-disc {
    color: #687188;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    line-height: 28px;
  
}
.Add-to-cart button{
    width: 100%;
    background: #ff324d;
    color: #fff;
    padding: 10px;
    border: none;
    border-radius: 4px;
}
.Add-to-cart {
    padding: 15px 0px;
}
.Quantity-btn span {
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    color: #292b2c;
    font-weight: 600;
}
.Quantity-btn {
    display: flex;
    gap: 10px;
    align-items: center;
}
.Quantity-btn button{
    border: none;
background: none;
}
.Category p span{
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    color: #292b2c;
    font-weight: 600;
}

.cart-link{
    font-size: 16px;
    font-weight: 600;
    line-height: 22px;
    color: #292b2c;
    margin-top: 35px;
    text-decoration: none;
}

.p-part-contant {
    display: flex;
    flex-direction: column;
    gap: 23px;
}

.Shopping-cart {
    padding: 60px 0px;
}
.Cart-c{
color: #FF324D;
}
.cart-content{

        display: flex;
        justify-content: space-between;
        padding-top: 50px;
    
}
.cart-content h3 {
    font-size: 14px;
    color: #292b2c;
    font-weight: 600;
    font-family: 'Poppins', sans-serif;
}
.cart-heading{
    font-family: 'Roboto';
    font-weight: 700;
    font-size: 35px;
    color: #141515;
    text-align: left;
}
.P-cart li{
    list-style: none;
}
.P-cart{
    padding: 30px 0px;
}

.P-cart li div {
    display: flex;
    justify-content: space-between;
}
.P-cart li div h2 {
    font-size: 14px;
    color: #525655;
    font-weight: 500;
    font-family: 'Poppins', sans-serif;
}
@media (max-width:425px) {
    .product-list-section ul {

        grid-template-columns: auto;
        justify-items: center;

    }

    .product-list-section {
        padding: 35px 10px;
    }

    .Home-Page-Product-title::before {
        left: 44%;
    }
    .p-part-contant{
     padding-top: 30px ;
     gap: 15px;
    }
    .Single-product-page {
        padding: 50px 0px;
    }
}

