 .icon-box {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  .Announcement-bar h2 {
    text-transform: capitalize;
    font-size: 14px;
    color: #292b2c;
    font-weight: 400;
    font-family: 'Poppins', sans-serif;
  }
  
  section.Announcement-bar {
    padding: 10px 0px 5px 0px;
    border-bottom: 1px solid #eeeeee;
  }
  .section-1 {
    padding: 5px 0px;
}
  .Ann-icon {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .Ann-icon i.bi.bi-person-add {
    font-size: 17px;
  }
  
  .icon-box i {
    font-size: 20px;
    color: #525655;
  }
  
  a.nav-link.active {
    color: #ff324d !important;
  }
  
  a.nav-link {
    font-weight: 600;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    color: #292b2c;
    margin: 0px 4px;
  }

  .header-section-container {
    display: grid;
    grid-template-columns: 15% 80% 5%;
    justify-items: center;
}
.icon-box .cart-link {
  margin: 0;
}
  .mobile-logo{
   display: none;
  }
  @media (max-width:425px) {
.icon-box {
    height: 60px;
}
  .mobile-logo{
    display: block;
   }
   .desktop-logo{
    display: none;
   }
   .header-section-container{
    display: grid;
    grid-template-columns: 20% 60% 20%;
  }
}